import React from "react";
import UsersStyles from './users.module.css'
import * as  axios from 'axios';
import userPhoto from '../../../src/StandardAvatar/images/StanadartUser.jpg';

class Users extends React.Component {
    componentDidMount() {
        axios.get(`https://social-network.samuraijs.com/api/1.0/users?count=${this.props.pageSize}&page=${this.props.currentPage}`,
            {
                withCredentials: true,
                headers: {"API-KEY":"94e4d73c-3c12-433f-9191-6d1113ffafc1"}
            }).then(response => {

            this.props.setUsers(response.data.items);
debugger;
            this.props.setTotalUsersCount(response.data.totalCount);
        });
    }
onPageChanged=(pageNumber)=> {     // зачем 2й запрос\?
      this.props.setCurrentPage(pageNumber);
    axios.get(`https://social-network.samuraijs.com/api/1.0/users?count=${this.props.pageSize}&page=${pageNumber}`).then(response => {

        this.props.setUsers(response.data.items);

    });
};

    render() {
        let pagesCount = Math.ceil(this.props.totalUsersCount / this.props.pageSize);
        let pages = [];
        for (let i = 1; i <= pagesCount; i++) {
           pages=[...pages,i];
        }
debugger;
        return <div>
            <div>
                {pages.map(p => {

                    return <span className={this.props.currentPage === p && UsersStyles.selectedPage} onClick={(e) => {
                     this.onPageChanged(p)}}>{p}</span>
                })}


            </div>

            {
                this.props.users.map(u => <div key={u.id}>
            <span>

                <div><img src={u.photos.small != null ? u.photos.small : userPhoto} className={UsersStyles.userPhoto}/></div>
                <div>{u.followed ? <button onClick={() => {
                    this.props.unfollow(u.id)
                }}>Unfollow</button> : <button onClick={() => {
                    this.props.follow(u.id)
                }}>Follow</button>}</div>
             </span>

                    <span>
                  <span>
                      <div>{u.name}</div>
                  <div>{u.Status}</div>

                  </span>
                  <span>
                      <div>{'u.location.country'}</div>
                      <div>{'u.location.city'}</div>

                  </span>

            </span>


                </div>)
            }

        </div>
    }


}


export default Users;


